export const API = (() => {
    const API_KEY = 'cd8616dd1859d5e422a87d51ad82a0b9';
    const BASE_URL = 'https://api.themoviedb.org/3';

    const getData = url => fetch(url);

    return {
        fetchMovieCast: async movieId => {
            const response = await getData(`${BASE_URL}/movie/${movieId}/credits?api_key=${API_KEY}`);

            return response.json();
        },
        fetchMovieSearched: async query => {
            const response = await getData(`${BASE_URL}/search/movie?api_key=${API_KEY}&query=${query}`);

            return response.json();
        },
        fetchWeekTrending: async () => {
            const response = await getData(`${BASE_URL}/trending/movie/week?api_key=${API_KEY}`);

            return response.json();
        },
        fetchMovieDetails: async movieId => {
            const response = await getData(`${BASE_URL}/movie/${movieId}?api_key=${API_KEY}&language=en-US`);

            return response.json();
        },
        fetchTrailer: async movieId => {
            const response = await getData(`${BASE_URL}/movie/${movieId}/videos?api_key=${API_KEY}&language=en-US`);

            return response.json();
        },

        fetchSimilarMovies: async movieId => {
            const response = await getData(`${BASE_URL}/movie/${movieId}/similar?api_key=${API_KEY}&language=en-US&page=1`);

            return response.json();
        },
    }

})();